import React, { useState,useReducer } from "react";
import DynamicMeal from "./DynamicMeal";
import { Tabs, Tab } from "react-bootstrap";
import { restaurant } from "../../../models/restaurant";
import RestaurantReducer from "../../../reducers/reducers";
import { SET_MEALS } from "../../../utilities/constants";

export default function Meals(props) {
  const [key, setKey] = useState("monday");
  const [meals, setMeals] = useState([]);
  const [user, setUser] = useReducer(RestaurantReducer, restaurant);
  const handleContinue = (e) => {
    setUser({
      type: SET_MEALS,
      payload:meals
    })
    props.goToStep(4);
  };
  const handleBack = (e) => {
    e.preventDefault();
    props.goToStep(2);
  };
  const addMeal = (index, meal) => {
    setMeals([...meals, meal]);
  };
  return (
    <fieldset>
      <Tabs
        className="tabs-container mt-1"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab eventKey="monday" title="Monday">
          <DynamicMeal index={1} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="tuesday" title="Tuesday">
          <DynamicMeal index={2} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="wednesday" title="Wednesday">
          <DynamicMeal index={3} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="thursday" title="Thursday">
          <DynamicMeal index={4} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="friday" title="Friday">
          <DynamicMeal index={5} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="saturday" title="Saturday">
          <DynamicMeal index={6} addMeal={addMeal} />
        </Tab>
        <Tab eventKey="sunday" title="Sunday">
          <DynamicMeal index={7} addMeal={addMeal} />
        </Tab>
      </Tabs>

      <div className="col-lg-12 justify-content-end">
        <button
          type="button"
          className="btn btn-primary float-right mr-2 "
          onClick={handleContinue}
        >
          Next
        </button>
        <button
          type="button"
          className="btn btn-default float-right mr-2 "
          onClick={handleBack}
        >
          Previous
        </button>
      </div>
    </fieldset>
  );
}
