import {
  BASIC_INFO,
  SET_BANK_INFO,
  SET_DOCUMENTS,
  SET_MEALS,
  SET_PLANS,
  SET_RESTAURANT,
} from "../utilities/constants";
const initialState = {
  restaurant: [],
};
export default function RestaurantReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case BASIC_INFO:
      return { ...state, ...payload };
    case SET_DOCUMENTS:
      return { ...state, ...payload };
    case SET_MEALS:
      return { ...state, ...payload };
    case SET_PLANS:
      return { ...state, ...payload };
    case SET_BANK_INFO:
      return { ...state, ...payload };
    case SET_RESTAURANT:
      return { ...state};
    default:
      return state;
  }
}
