import axios from "axios";
import React, { useState, useEffect, useReducer } from "react";
import RestaurantReducer from "../../../reducers/reducers";
import { SET_PLANS } from "../../../utilities/constants";
import { restaurant } from "../../../models/restaurant";
export default function Plans(props) {
  const [plan, setPlan] = useState({});
  const [customer2price, setCustomer2Price] = useState("");
  const [customer15price, setCustomer15Price] = useState("");
  const [customer30price, setCustomer30Price] = useState("");
  const [inputPlan, setInputPlan] = useState({});
  const [user, setUser] = useReducer(RestaurantReducer, restaurant);
  const handleContinue = (e) => {
    const { base_2price, base_15price, base_30price } = inputPlan;
    const plans = {
      twoPlan: {
        base_2price: base_2price,
        customer_2price: customer2price,
      },
      fifteenPlan: {
        base_15price: base_15price,
        customer_15price: customer15price,
      },
      thirtyPlan: {
        base_30price: base_30price,
        customer_30price: customer30price,
      },
    };
    setUser({
      type: SET_PLANS,
      payload: plans,
    });
    props.goToStep(5);
  };
  const handleBack = (e) => {
    props.goToStep(3);
  };
  const onBasePriceChange = (event) => {
    const { name, value } = event.target;
    const { twoPlan, fifteenPlan, thirtyPlan } = plan;
    setInputPlan((prevState) => ({ ...prevState, [name]: value }));
    name === "base_2price"
      ? setCustomer2Price(parseFloat(value) + parseFloat(twoPlan))
      : name === "base_15price"
      ? setCustomer15Price(parseFloat(value) + parseFloat(fifteenPlan))
      : setCustomer30Price(parseFloat(value) + parseFloat(thirtyPlan));
  };
  useEffect(() => {
    axios
      .get("https://munkybox-admin.herokuapp.com/api/plans")
      .then((res) => {
        setPlan(res.data[0]);
      })
      .catch((err) => {
        console.log("====================================");
        console.log(err);
        console.log("====================================");
      });
  }, []);
  return (
    <fieldset>
      <p className="mt-2">
        <strong>2 Days {plan.twoPlan}</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price <strong className="text-danger">*</strong> &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_2price"
              onChange={(e) => onBasePriceChange(e)}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer2price"
              defaultValue={customer2price}
            />
          </div>
        </div>
      </div>
      <p>
        <strong>15 days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price <strong className="text-danger">*</strong> &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_15price"
              onChange={(e) => onBasePriceChange(e)}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer15price"
              defaultValue={customer15price}
            />
          </div>
        </div>
      </div>
      <p>
        <strong>30 Days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price <strong className="text-danger">*</strong> &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_30price"
              onChange={(e) => onBasePriceChange(e)}
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer30price"
              defaultValue={customer30price}
            />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2 "
            onClick={handleContinue}
          >
            Next
          </button>
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
